import React from 'react';
import classNames from 'classnames';

export type ChipColor = 'primary' | 'lighter' | 'darker' | 'warning' | 'critical';
export interface ChipProps extends React.HTMLAttributes<HTMLDivElement> {
  readonly variant?: 'compact' | 'relaxed';
  readonly rounded?: 's' | 'm';
  readonly color?: ChipColor;
}

export const Chip: React.FC<ChipProps> = ({
  variant = 'relaxed',
  rounded = 'm',
  color = 'primary',
  className,
  children,
  ...props
}) => (
  <div
    className={classNames(
      {
        'inline-block': true,
        rounded: rounded,
        'bg-primary': color === 'primary',
        'bg-primaryLighter': color === 'lighter',
        'bg-darker': color === 'darker',
        'bg-warning': color === 'warning',
        'bg-critical': color === 'critical',
        'py-3 px-6': variant === 'relaxed',
        'p-1': variant === 'compact',
      },
      className
    )}
    {...props}
  >
    {children}
  </div>
);
